.button {
  width: 176px;
  height: 44px;
  cursor: pointer;
  transition: background-color var(--main-transition);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: baseline;
  padding: 11px 37px;
  font-weight: 700;
  line-height: 1.28571;
  display: flex;
}

@media screen and (min-width: 768px) {
  .button {
    font-size: 16px;
    line-height: 1.3125;
  }
}

.button__icon {
  width: 5px;
  height: 8px;
  transition: background-color var(--main-transition);
  margin-left: 10px;
}

.button--white {
  color: var(--accent-color);
  background-color: var(--primary-text-color);
  stroke: var(--accent-color);
}

.button--red {
  color: var(--primary-text-color);
  background-color: var(--button-second-accent-bgcolor);
  stroke: var(--primary-text-color);
}

.button--white:hover, .button--white:focus, .button--white:hover .button__icon, .button--white:focus .button__icon {
  background-color: var(--button-hover-bgcolor);
}

.button--white:hover, .button--white:focus {
  box-shadow: var(--button-shadow);
}

.button--red:hover, .button--red:focus, .button--red:hover .button__icon, .button--red:focus .button__icon {
  background-color: var(--accent-slider-dot-color);
}

