.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @media screen and (min-width: 1200px) {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }

  &__gallery {
    width: 100%;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width: 768px) {
      max-width: 768px;
    }

    @media screen and (min-width: 1200px) {
      max-width: 1200px;
      padding-left: 16px;
      padding-right: 16px;
    }
  }

  &__footer {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;

    @media screen and (min-width: 768px) {
      max-width: 768px;
      padding-left: 16px;
      padding-right: 16px;
    }

    @media screen and (min-width: 1200px) {
      max-width: 1200px;
    }
  }

  &__contacts {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;

    @media screen and (min-width: 768px) {
      max-width: 768px;
      padding-left: 16px;
      padding-right: 16px;
    }

    @media screen and (min-width: 1200px) {
      max-width: 1200px;
    }
  }
}
